<template>
    <div>
        <el-button @click="handleBack">回到第一页</el-button>
        <el-table
            v-loading="isLoading"
            :data="students"
        >
            <el-table-column
                prop="name"
                label="姓名"
            >
            </el-table-column>
            <el-table-column label="性别">
                <template slot-scope="scope">
                    <el-tag
                        v-if="scope.row.gender==='男'"
                        type="success"
                    >男</el-tag>
                    <el-tag
                        v-else
                        type="warning"
                    >女</el-tag>
                </template>
            </el-table-column>
            <el-table-column
                prop="address"
                label="住址"
            >
            </el-table-column>
            <el-table-column
                prop="class.name"
                label="班级"
            >
            </el-table-column>
            <el-table-column
                prop="course.name"
                label="课程"
            >
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button
                        type="primary"
                        icon="el-icon-edit"
                        size="mini"
                        @click="handleEdit(scope.row._id)"
                        circle
                    ></el-button>
                    <el-button
                        type="danger"
                        icon="el-icon-delete"
                        size="mini"
                        @click="$store.dispatch('student/remove', scope.row._id)"
                        circle
                    ></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination
            background
            :current-page.sync="curPage"
            layout="prev, pager, next"
            :total="total"
        >
        </el-pagination>
    </div>
</template>

<script>
export default {
    computed: {
        students() {
            return this.$store.state.student.data.students;
        },
        total() {
            return this.$store.state.student.data.total;
        },
        curPage: {
            get() {
                return this.$store.state.student.data.curPage;
            },
            set(newPage) {
                this.$store.dispatch("student/fetch", newPage);
            }
        },
        isLoading() {
            return this.$store.state.student.data.isLoading;
        }
    },
    methods: {
        async handleBack() {
            this.curPage = 1;
        },
        handleEdit(id) {
            //代码跳转页面
            this.$router.push({
                name: "editstu",
                params: { id }
            });
        }
    }
};
</script>

